.fade-enter-active,
.fade-leave-active {
  -webkit-transition: 0.2s all;
  transition: 0.2s all;
}
.fade-enter {
  opacity: 0;
  -webkit-transform: translateX(50px);
          transform: translateX(50px);
}
.fade-leave-active {
  opacity: 0;
  -webkit-transform: translateX(-50px);
          transform: translateX(-50px);
}
